* { box-sizing: border-box; }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video, embed{
	padding: 0;
	margin: 0;
	border: 0;
	outline: 0;
	background: transparent;
	vertical-align: baseline;
	font-size: 100%;
	}
/* HTML5 reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
	}
body {
	line-height: 1;
	}
ol, ul {
	list-style: none;
}

a, a:link, a:visited {
  color: darken($color-pr,10%);
}

.left{
	text-align: left;
}
.right{
	text-align: right;
}
blockquote, q {
	quotes:none;
	}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
	}
:focus {
	outline: 0;
	}
input, select {
	vertical-align:middle;
	}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
	margin:0;
	padding:0;
	font-size:100%;
	background:transparent;
	text-decoration: none;
}
fieldset, img, abbr {
	border: 0;
}
del {
	text-decoration: line-through;
}
ins {
	text-decoration:none;
}
hr {
  display:block;
  height:1px;
  border:0;
  border-top:1px solid #cccccc;
  margin:1em 0;
  padding:0;
}
.clr{
	clear:both;
}
.center {
	text-align: center;
}
.left {
	text-align: left;
}
.right {
	text-align: right;
}

.btn.btn-pr {
  background: $color-pr;
  color: white;
  text-transform: uppercase;
}
.btn.btn-sec {
  background: darken($color-sec, 40%);
  color: white;
  text-transform: uppercase;
}


*::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color:    #ccc;
}
*:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color:    #ccc;
   opacity:  1;
}
*::-moz-placeholder { /* Mozilla Firefox 19+ */
   color:    #ccc;
   opacity:  1;
}
*:-ms-input-placeholder { /* Internet Explorer 10-11 */
   color:    #ccc;
}

body, html {
	font-family: 'PT Sans', sans-serif;
	font-weight: 400;
	font-size: 1em;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0);
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
}

.pw {
	width: $page-width;
	margin: 0 auto;
}

.grid-layout {
	display: table;
    width: 100%;
    margin: 0;
    direction: ltr;
    text-align: left;

    .grid-row {
    	display: table-cell;
    	vertical-align: top;
    	direction: ltr;
    }

    .grid-row:not(.grid-np) {
    	padding: 0;
    }

    .grid-row-10 {
    	width: 10%;
    }
    .grid-row-20 {
    	width: 20%;
    }
     .grid-row-25 {
    	width: 25%;
    }
    .grid-row-30 {
    	width: 30%;
    }
    .grid-row-33 {
      width: 33%;
    }
    .grid-row-40 {
    	width: 40%;
    }
    .grid-row-50 {
    	width: 50%;
    }
    .grid-row-60 {
    	width: 60%;
    }
    .grid-row-70 {
    	width: 70%;
    }
    .grid-row-80 {
    	width: 80%;
    }
    .grid-row-90 {
    	width: 90%;
    }
    .grid-row-100 {
    	width: 100%;
    }
}
.grid-layout.reverse {
	 direction: rtl;

    text-align: left;
}
.grid-layout:not(.grid-np) > .grid-row {
	padding: 0 20px !important;
}
.grid-layout:not(.grid-np) > .grid-row:first-child {
	padding: 0 20px 0 0 !important;
}
.grid-layout:not(.grid-np) > .grid-row:last-child {
	padding: 0 0 0 20px !important;
}
.grid-layout.reverse:not(.grid-np) > .grid-row:first-child {
	padding: 0 0 0 20px !important;
}
.grid-layout.reverse:not(.grid-np) > .grid-row:last-child {
	padding: 0 20px 0 0 !important;
}

.vra-top{ vertical-align: top !important; }
.vra-middle{ vertical-align: middle !important; }
.vra-bottom{ vertical-align: bottom !important; }

.right {
	text-align: right;
}
.center {
	text-align: center;
}
.left {
	text-align: left;
}

.clr {
  clear: both;
}

.website {
  display: table;
  width: $page-width;
  margin: 0 auto;
  margin-top: 20px;
  border-collapse: collapse;
}

h4 {
  font-size: 110%;
}

h3 {
  font-size: 130%;
}

h2 {
  font-size: 160%;
}

h1 {
  font-size: 200%;
}

img.aw {
  width: 100%;
  max-width: 100%;
}

.row.np {
  margin: 0;
}

.row.np > div[class*=col-] {
  padding: 0;
}

.form-text-md {
  line-height: 35px;
}

.form-control:focus {
    border-color: $color-pr;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(245, 100, 140, 0.6);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(245, 100, 140, 0.6);
}

.alert-danger {
    background-color: #FFE7E7;
    background-image: none;
    border-color: #ebccd1;
    color: #E24855;
}

.show-on-mobile {
  display: none !important;
}

.product-view .product-desc {

  img {
    max-width: 100%;
  }
}
